import React from "react";
import { BrowserRouter, Routes, Route } from "react-router-dom";
import { SocketProvider } from "./SocketContext";
import SocketListener from "./SocketListenner";
import InactivityMonitor from "./InactivityMonitor";
import CloseMonitor from "./CloseMonitor2";
import RouteGuard from "./RouteGuard";
import Login from "./LoginCompo2";
import Index from "./Index";
import Spinner from "./Spinner";
import { useDispatch, useSelector } from "react-redux";
import Desk from "./DeskCompo2";
import { v4 as uuidv4 } from "uuid";
import { sToken } from "../features/users/usersSlice";

function App() {
  const allowedRoutes = ["/", "/login", "/main/posicionconsolidada"];
  const user = useSelector((e) => e.users);
  const dispatch = useDispatch();

  // Generar o recuperar el sessionID
  let sessionID = JSON.parse(localStorage.getItem("sessionID"));
  if (!sessionID) {
    sessionID = uuidv4();
    localStorage.setItem("sessionID", JSON.stringify(sessionID));
  }

  if (sessionID) dispatch(sToken({ token: sessionID }));

  return (
    <>
      <SocketProvider>
        <BrowserRouter>
          <SocketListener dispatch={dispatch} />
          <InactivityMonitor inactivityLimit={5 * 60 * 1000} />
          <RouteGuard allowedRoutes={allowedRoutes} />
          <CloseMonitor /> 
          <Routes>
            <Route path="/" element={<Index />} />
            <Route path="/login" element={<Login />} />
            <Route path="/main/posicionconsolidada" element={<Desk />} />
            <Route path="*" element={<Index />} />
          </Routes>
        </BrowserRouter>
      </SocketProvider>
      {user.loading && <Spinner />}
    </>
  );
}

export default App;
