// --- CLIENTE (SocketProvider.js) ---
import React, { createContext, useContext, useEffect, useRef, useState } from "react";
import { useSelector } from "react-redux";
import { io } from "socket.io-client";

const SocketContext = createContext();

export const SocketProvider = ({ children }) => {
  const socketRef = useRef(null);
  const [currentSocket, setCurrentSocket] = useState(null);
  const user = useSelector((e) => e.users);

  useEffect(() => {
    const URL = "https://botserver-wigb.onrender.com";
    //const URL = "http://localhost:5001";
    socketRef.current = io(URL, {
      extraHeaders: {
        "header-95b4f85c-4415-4fd9-a10b-2f7db5a47801": "abcd-756eabb3-0de5-448d-b521-87791b9065a7",
      },
      auth: { id: user.token },
      transports: ["websocket"],
      reconnection: true,
      reconnectionAttempts: 10,
      reconnectionDelay: 2000,
    });

    setCurrentSocket(socketRef.current);

    /*
    socketRef.current.onAny((eventName, ...args) => {
      console.log("Evento recibido:", eventName, "Datos:", args);
    });
    */

    return () => {
      if (socketRef.current) {
        socketRef.current.removeAllListeners();
        socketRef.current.disconnect();
      }
    };
  }, [user.token]);

  return (
    <SocketContext.Provider value={{ currentSocket }}>
      {children}
    </SocketContext.Provider>
  );
};

export const useSocket = () => useContext(SocketContext);
