import React from "react";
import style from "./css/login.module.css";
import { motion } from "framer-motion";
import { useDispatch } from "react-redux";
import { sError } from "../features/users/usersSlice";

function Error() {
  const [timeLeft, setTimeLeft] = React.useState(5); // 120 segundos = 2 minutos
  const dispatch = useDispatch();

  React.useEffect(() => {
    if (timeLeft > 0) {
      const timer = setInterval(() => {
        setTimeLeft((prevTime) => prevTime - 1);
      }, 1000);
      return () => clearInterval(timer);
    } else {
      dispatch(sError({e: false}));
    }
  }, [timeLeft]);
  const handleE = () => {
    dispatch(sError({e: false}))
  };
  return (
    <>
      <motion.div
        initial={{ y: "-100%" }}
        animate={{ y: 0 }}
        exit={{ y: "-100%" }}
        transition={{ duration: 0.8 }}
        style={{
          position: "fixed",
          top: 0,
          left: 0,
          right: 0,
          height: "100%",
          display: "flex",
          alignItems: "center",
          justifyContent: "center",
          zIndex: 1000,
        }}
      >
        <div className={style["cdk-overlay-container"]}>
          <div
            className={style["cdk-global-overlay-wrapper"]}
            dir="ltr"
            style={{ justifyContent: "center", alignItems: "flex-end" }}
          >
            <div
              id="cdk-overlay-1"
              className={style["cdk-overlay-pane"]}
              style={{
                pointerEvents: "auto",
                position: "static",
                marginBottom: "0px",
              }}
            >
              <div
                className={
                  style["mat-snack-bar-container"] +
                  " " +
                  style["ng-tns-c16-3"] +
                  " " +
                  style["ng-trigger"] +
                  " " +
                  style["ng-trigger-state"] +
                  " " +
                  style["_snackBar"] +
                  " " +
                  style["mat-snack-bar-center"] +
                  " " +
                  style["ng-star-inserted"]
                }
                role="alert"
                style={{ transform: "translateY(0%)" }}
              >
                <div
                  className={
                    style["mat-simple-snackbar"] +
                    " " +
                    style["ng-tns-c17-4"] +
                    " " +
                    style["ng-trigger"] +
                    " " +
                    style["ng-trigger-contentFade"] +
                    " " +
                    style["ng-star-inserted"]
                  }
                >
                  <span className={style["ng-tns-c17-4"]}>
                    Autenticacin incorrecta.
                  </span>
                  <div
                    className={
                      style["mat-simple-snackbar-action"] +
                      " " +
                      style["ng-tns-c17-4"] +
                      " " +
                      style["ng-star-inserted"]
                    }
                  >
                    <button
                      className={
                        style["ng-tns-c17-4"] + " " + style["mat-button"]
                      }
                      mat-button=""
                      onClick={handleE}
                    >
                      <span className={style["mat-button-wrapper"]}>
                        Aceptar
                      </span>
                      <div
                        className={
                          style["mat-button-ripple"] + " " + style["mat-ripple"]
                        }
                        matripple=""
                      ></div>
                      <div className={style["mat-button-focus-overlay"]}></div>
                    </button>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </motion.div>
    </>
  );
}

export default Error;
