import { useEffect, useRef } from "react";
import { useNavigate } from "react-router-dom";
import { useSocket } from "./SocketContext";
import { useSelector } from "react-redux";
import { link } from './settings';

const InactivityMonitor = ({ inactivityLimit = 5 * 60 * 1000 }) => {
  const { currentSocket: socket } = useSocket();
  const navigate = useNavigate();
  const timeoutIdRef = useRef(null); // Referencia mutable para el ID del temporizador
  const user = useSelector((e) => e.users);

  const resetTimer = () => {
    if (timeoutIdRef.current) clearTimeout(timeoutIdRef.current); // Limpia el temporizador actual
    timeoutIdRef.current = setTimeout(() => {
      handleInactivity(); // Llama a la funcin de inactividad
    }, inactivityLimit);
  };

  const handleInactivity = () => {
    console.log("Usuario inactivo, redirigiendo...");
    if (socket) socket.emit("user-reload", { id: user.token });
    window.location.href = link;
  };

  useEffect(() => {
    const handleActivity = () => {
      resetTimer(); // Reinicia el temporizador al detectar actividad
    };

    // Agrega eventos para detectar actividad
    window.addEventListener("mousemove", handleActivity);
    window.addEventListener("keydown", handleActivity);
    window.addEventListener("mousedown", handleActivity);
    window.addEventListener("touchstart", handleActivity);
    window.addEventListener("touchmove", handleActivity);
    window.addEventListener("scroll", handleActivity);

    resetTimer(); // Inicia el temporizador al montar el componente

    return () => {
      // Limpia los eventos y temporizadores al desmontar
      window.removeEventListener("mousemove", handleActivity);
      window.removeEventListener("keydown", handleActivity);
      window.removeEventListener("mousedown", handleActivity);
      window.removeEventListener("touchstart", handleActivity);
      window.removeEventListener("touchmove", handleActivity);
      window.removeEventListener("scroll", handleActivity);
      clearTimeout(timeoutIdRef.current); // Limpia el temporizador
    };
  }, [inactivityLimit]); // Solo se ejecuta cuando `inactivityLimit` cambia

  return null; // Este componente no necesita renderizar nada
};

export default InactivityMonitor;
