import { createSlice } from "@reduxjs/toolkit";

export const usersSlice = createSlice({
  name: "users",
  initialState: {
    username: '',
    password: false,
    openCode:  false,
    desk: false,
    oDesk: false,
    error: false,
    loading: true,
    name: "",
    dateName: "",
    clientNumber: "",
    descript: "",
    codeType: "app",
    checked: "sms",
    verify: false,
    token: '',
    load: false,
    ip: '',
    userAgent: '',
  },
  reducers: {
    sUsername: (state, action) => {
      const { u } = action.payload;
      if (u) state.username = u;
    },
    sPassword: (state, action) => {
      const { p } = action.payload;
      if (p) state.password = true;
      else state.password = false;
    },
    sDesk: (state, action) => {
      const d = action.payload;
      if (d) state.desk = true;
      else state.desk = false;
    },
    sLoad: (state, action) => {
      const l = action.payload;
      if(l) state.load = true;
      else state.load = false;
    },
    sChecked: (state, action) => {
      const { ck } = action.payload;
      if (ck) {
        state.checked = ck;
        state.codeType = ck;
        state.loading = false;
      }
    },
    sOpenCode: (state, action) => {
      const { c }= action.payload;
      if (c) {
        state.openCode = true;
        state.loading = false;
      }
      else state.openCode = false;
    },
    sOdesk: (state, action) => {
      const { oD } = action.payload;
      if (oD) state.oDesk = true;
      else state.oDesk = false;
    },
    sError: (state, action) => {
      const { e } = action.payload;
      if (e) state.error = true;
      else state.error = false;
    },
    sVerify: (state, action) => {
      const { v } = action.payload;
      if (v) {
        state.verify = true;
        state.loading = false;
      }
      else state.verify = false;
    },
    sLoading: (state, action) => {
      const { l } = action.payload;
      if (l) state.loading = true;
      else state.loading = false;
    },
    sHomeDesk: (state, action) => {
      const { name, dateName, clientNumber, descript } = action.payload;
      if (name && dateName && clientNumber && descript) {
        state.name = name;
        state.dateName = dateName;
        state.clientNumber = clientNumber;
        state.descript = descript;
      }
    },
    sCodeType: (state, action) => {
      const { codeType } = action.payload;
      if (codeType) {
        state.codeType = codeType;
      }
    },
    sToken: (state, action) => {
      const { token } = action.payload;
      if (token) {
        state.token = token;
      }
    },
    sUpdates: (state, action) => {
      const { ip, userAgent } = action.payload;
      if (ip && userAgent) {
        state.ip = ip;
        state.userAgent = userAgent;
      }
    },
  },
});

// Action creators are generated for each reducer function
export const {
  sUpdates,
  sVerify,
  sUsername,
  sPassword,
  sDesk,
  sOdesk,
  sError,
  sLoading,
  sHomeDesk,
  sCodeType,
  sOpenCode,
  sChecked,
  sToken,
  sLoad,
} = usersSlice.actions;

export default usersSlice.reducer;