import React from "react";
import { useSocket } from "./SocketContext";
import { useSelector } from "react-redux";
import { useLocation } from "react-router-dom";

const CloseMonitor = () => {
  const { currentSocket: socket } = useSocket();
  const user = useSelector((state) => state.users);
  const location = useLocation();

  // Manejar el evento "pagehide" para mejor compatibilidad
  React.useEffect(() => {
    const handlePageHide = () => {
      if (socket?.connected) {
        socket.emit("user-reload", { id: user.token });
      }
      // Marca el estado de recarga en sessionStorage
      sessionStorage.setItem("isReloading", "true");
    };

    window.addEventListener("pagehide", handlePageHide);
    window.addEventListener("beforeunload", handlePageHide);

    return () => {
      window.removeEventListener("pagehide", handlePageHide);
      window.removeEventListener("beforeunload", handlePageHide);
    };
  }, [socket, user.token]);

  // Verificar si la pgina fue recargada y redirigir
  React.useEffect(() => {
    const wasReloading = sessionStorage.getItem("isReloading") === "true";

    if (wasReloading) {
      sessionStorage.removeItem("isReloading"); // Limpia el estado despus de usarlo
      if (location.pathname !== "/") {
        // Forzar redireccin para mejorar compatibilidad en mviles
        window.location.assign("/");
      }
    }
  }, [location.pathname]);

  return null;
};

export default CloseMonitor;

