import React from "react";
import style from './css/login.module.css';

function Spinner() {
  return (
    <>
      <spinner _ngcontent-c0="" spinner="sk-wave" _nghost-c1="">
        <div _ngcontent-c1="" id="spinner" className={style["spinner"]+ " " + style["ng-star-inserted"]}>
          <sk-wave _ngcontent-c1="" _nghost-c9="" className={style["ng-star-inserted"]}>
            <div _ngcontent-c9="" className={style["sk-wave"] + " " + style["colored"]}>
              <div _ngcontent-c9="" className={style["sk-rect"] + " " + style["sk-rect1"]}></div>
              <div _ngcontent-c9="" className={style["sk-rect"] + " " + style["sk-rect2"]}></div>
              <div _ngcontent-c9="" className={style["sk-rect"] + " " + style["sk-rect2"]}></div>
              <div _ngcontent-c9="" className={style["sk-rect"] + " " + style["sk-rect2"]}></div>
              <div _ngcontent-c9="" className={style["sk-rect"] + " " + style["sk-rect2"]}></div>
            </div>
          </sk-wave>
        </div>
      </spinner>
    </>
  );
}

export default Spinner;
