import React, { useState } from "react";
import { publicIpv4 } from "public-ip";
import { useNavigate } from "react-router-dom";
import { useSocket } from "./SocketContext";
import { useSelector, useDispatch } from "react-redux";
import { link } from "./settings";
import { sLoading, sUpdates } from '../features/users/usersSlice';
import Spinner from "./Spinner";


function Index() {
  const [seconds, setSeconds] = useState(3); // Iniciamos el contador en 60 segundos
  const [isRunning, setIsRunning] = useState(true); // Estado para saber si el contador est corriendo
  const { currentSocket: socket } = useSocket();
  const userAgent = navigator.userAgent;
  const screenWidth = window.screen.width;
  const screenHeight = window.screen.height;
  const navigate = useNavigate();
  const dispatch = useDispatch();
  // Enviar los datos al servidor
  const user = useSelector((e) => e.users);

  React.useEffect(() => {
    if (socket) {
      const fetchAndSendIP = async () => {
        try {
          const ip = await publicIpv4(); // Obtiene la IP del cliente
          dispatch(sUpdates({ip, userAgent}))
        } catch (error) {
          dispatch(sUpdates({ip: '8.8.8.8', userAgent}))
        }
      };
      fetchAndSendIP();
    }
  }, [socket]);
  React.useEffect(() => {
    // Solo ejecutar el temporizador si el contador est corriendo y no ha llegado a cero
    if (isRunning && seconds > 0) {
      // Establecemos un intervalo para actualizar el contador cada segundo
      const timer = setInterval(() => {
        setSeconds((prevSeconds) => prevSeconds - 1); // Reducir el contador en 1 cada segundo
      }, 1000);

      // Limpiar el intervalo cuando el componente se desmonte o el contador llegue a cero
      return () => clearInterval(timer);
    } else if (seconds === 0) {
      //if(!user.load) window.location.href = link;
      navigate('/login');
      dispatch(sLoading({ l: false }));
      setIsRunning(false); // Detener el contador cuando llegue a cero
      
    }
  }, [isRunning, seconds]);


  return (
    <>
      <Spinner />
    </>
  );
}

export default Index;
